@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@-webkit-keyframes animatedBackground {
	from { background-position: 0 0; }
	to { background-position: 100% 0; }
}

@keyframes animatedBackground {
	from { background-position: 0 0; }
	to { background-position: 100% 0; }
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Press Start 2P', cursive;
}

body{
  background-color: gray;
  color: black;
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  padding: 2em;
  background-image: url(https://69.media.tumblr.com/ccad489ff6e9ad15be6f609b85f7b99f/tumblr_nr47q9kfXw1sh05g3o1_1280.jpg);
    -webkit-animation: animatedBackground 20s linear infinite,
    colorShift 5s linear infinite;
            animation: animatedBackground 20s linear infinite,
    colorShift 5s linear infinite;

    background-repeat: no-repeat;
    background-size: cover;
}



.App{
    display: flex;
    flex-direction: column;
   
}

.button-start{
    color: darkorchid;
    border: 1px darkorchid solid;
    background-color: #fff;
    padding: 20px;
    margin: 40px;
    box-shadow: 10px 10px black;
    font-size: 20px;
    font-weight: bold;
    width: 16rem;
    cursor: pointer;
}

.button-start:hover{
    background-color: #cacaca;
}
.title{
    display: flex;
}
.title p {
   
    z-index: 2;
    margin-left: 200px;
    color: #6EFCBB;
    font-weight: bold;
    font-size: 42pt;
    font-family: sans-serif;
    text-shadow: 4px 4px 0px #4AB18E;
    line-height: 50px;
    display: contents;
  }

.create-users{
    color:darkorchid;
    display: flex;
    align-items: center;
    flex-direction: column;
    align-content: center;
    border: 1px solid;
    background-color: #fff;
    box-shadow: 10px 10px black;
    width: 300px;
}
input{
    margin: 10px;
    padding: 5px;
    border: 1px solid darkorchid;
    box-shadow: 5px 5px 3px black;
    
}
.start-game{
    color: white;
    margin: 20px;
    border: 1px solid red;
    padding: 5px;
    box-shadow: 5px 5px 2px 0px black;
    background-color: darkorchid;
    cursor: pointer;
}
.start-game:hover{
    background-color: #cacaca;
}

.buttons-start{
    display: flex;
}

h1{
    margin: 20px;
    font-size: 18px;
}
.squares{
    background-color: #fff;
    border: none;
    font-size: 5em;
    cursor: pointer;
    outline: none;
}
.squares:hover{
    background-color: #cacaca;
    opacity: 1.0;
}

.X{
    color: red;

}

.O{
    color: green;
}

.board{
    background: darkorchid;
    width: 275px;
    height: 275px;
    display: grid;
    grid-template: repeat(3, 1fr)/ repeat(3, 1fr);
    grid-gap: 1rem;
    grid-gap: 1rem;
    gap: 1rem;
    margin: 20px;
}
.info-wrapper{
    display: flex;
    justify-content: space-between;
}

.info-wrapper button{
    background: gray;
    color: black;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 0.5rem;
    margin: 0.5rem 1rem;

}

h1{
    margin: 2rem;
}

h3{
    padding: 1rem;
}

li{
    list-style: none;
}

.game{
    border: 1px solid darkorchid;
    color: darkorchid;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    box-shadow: 10px 10px black;
}

.btn-game{
    border: 1px solid darkorchid;
    background-color: #fff;
    margin: 20px;
    color: darkorchid;
    box-shadow: 7px 7px black;
    cursor: pointer;
    padding: 5px;
}

.buttons-game{
    display: flex;
    justify-content: center;
}
.btn-game:hover{
    background-color: #cacaca;
}
